//
// Tabs
// --------------------------------------------------

.nav-tabs {
    border-bottom-width: 2px;
    
    > li {
        margin-bottom: -2px;
        
        > a {
            border-width: 2px;
        }
        
        &.active > a,
        &.active > a:hover,
        &.active > a:focus {
            border-width: 2px;
        }
    }
}

.nav-outline {
    position: relative;
    border-bottom: 0;
    
    white-space: nowrap;
    

    > li {
        white-space: normal;
        
        > a {
            border-color: transparent;
            color: #666;
            
            margin-bottom: 0;
        }
        
        &.open > a,
        &.active > a {
            font-weight: 600;
        }
        
        > a:hover {
            border-color: transparent;
        }
        
        > a:focus {
            outline: 5px auto -webkit-focus-ring-color;
            outline-offset: -2px;
        }
        
        > a:hover,
        > a:focus,
        &.open > a,
        &.open > a:focus,
        &.open > a:hover {
            background-color: transparent;
            
        }

        &.active > a,
        &.active > a:focus,
        &.active > a:hover {
            background-color: transparent;
            border-color: transparent;
        }
    }

    .progress {
        clear: both;
        height: 2px;
        margin-bottom: 0;
    }
    
    .progress-bar {
        position: relative;
        transition: none;
    }
    
    &.unanimated .progress-bar {
        transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    }
    
    &.nav-justified {
        > li {
            display: table-cell;
            width: 1%;
            
            > a {
                border-bottom: 0;
            }
            
            &.active > a,
            &.active > a:focus,
            &.active > a:hover {
                background-color: transparent;
                border-color: transparent;
            }
        }
    } 
}
